.mentorship-container-main {
  padding-top: 90px;
  padding-bottom: 90px;
}
.mentorship-container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
}
.mentorship-container h1 {
  color: white;
  text-align: center;
  font-size: 40px;
  font-size: 50px;
  letter-spacing: 8px;
}
.mentorship-container .info-container .mentorship-border {
  background-color: var(--clr-blue);
  height: 3px;
  border: none;
  width: 85px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
}
.mentorship-container .banner {
  width: 50%;
  height: 100%;
  object-fit: cover;
}

.mentorship-container .info-container {
  width: 50%;
  margin-right: 3rem;
}

.mentorship-container h2 {
  font-size: 36px;
  font-weight: 600;
  line-height: 49px;
}

.mentorship-container ul {
  font-style: normal;
  font-weight: 450;
  font-size: 20px;
  line-height: 145%;
  color: var(--clr-secondary);
  padding: 0 1.25rem 1.25rem 1.25rem;
}

.mentorship-container li {
  display: flex;
  padding: 1.25rem;
  text-align: start;
}
.mentorship-container li img {
  width: 30px;
  height: 30px;
}

.mentorship-container ul .highlight {
  color: var(--clr-white);
}
.roadmap-link {
  background-color: var(--clr-blue);
  color: white;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 145%;
  padding: 12px 24px 12px 24px;
  border-radius: 30px;
}
.courses-link {
  text-align: center;
}

.courses-link button {
  color: #ffffff;
  text-align: center;
  font-size: 1.5em;
  margin-top: 3rem;
  background-color: var(--clr-blue);
  color: white;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 145%;
  padding: 12px 24px;
  border-radius: 30px;
  margin-left: auto;
  margin-right: auto;
  border: none;
}
.roadmap-link img {
  height: 12px;
  width: 16px;
  margin-left: 0.5rem;
}

@media screen and (max-width: 1000px) {
  .mentorship-container {
    flex-direction: column;
    align-items: center;
    max-width: 90%;
  }

  .mentorship-container .banner {
    width: 100%;
    height: 30vh;
    order: 1;
  }

  .mentorship-container .info-container {
    width: 100%;
    margin: 2rem 0;
    order: 2;
  }
}

@media screen and (max-width: 650px) {
  .mentorship-container h2 {
    font-size: 24px;
    line-height: 32px;
  }

  .mentorship-container ul {
    font-size: 15px;
    padding: 0 0 1.25rem 1.25rem;
  }
}
